import { CreateServiceDto } from "../types/dtos/service.dto";

export type ServiceForm = {
  serviceCode: string;
  name: string;
  description: string;
  price: number;
  duration: number;
  observations: string;
};

export const getDefaultServiceForm = (): ServiceForm => ({
  serviceCode: '',
  name: '',
  description: '',
  price: 0.0,
  duration: 0,
  observations: ''
});

export const convertServiceFormToDto = (form: ServiceForm): CreateServiceDto => ({
  serviceCode: form.serviceCode.trim() || null,
  name: form.name.trim(),
  description: form.description.trim() || null,
  price: form.price,
  duration: form.duration || null,
  observations: form.observations.trim() || null
});